@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
.logo{grid-area:logo;margin-right:0.3vw;place-self:center}.logo .app-logo{width:100%;height:100%}.logo-text{grid-area:logo-text;display:flex;align-items:center;font-size:3.2rem;font-weight:500;color:#34373f}.logo-sub-text{grid-area:logo-sub-text;display:flex;align-items:center;font-size:1.6rem;font-weight:400;color:#9faab3}.logo-container{display:grid;grid-template-columns:4vw 40vw;grid-template-rows:auto;grid-template-areas:"logo logo-text" "logo logo-sub-text"}

.button{display:inline-block;border-radius:0.5vw;border:1px solid #8270df;box-sizing:border-box;padding:0.1vw 0.8vw;color:#6f7d89;font-size:1.4rem;cursor:pointer}.button:hover{border:1px solid #8270df;background:#8270df;color:#ffffff}

.nav-container{margin-top:2vw}.nav-container .button{margin-right:1vw}.nav-container .button:last-child{margin-right:0}

.separator{border:2px solid #8270df}

.header-container{margin:1vw 2vw 1vw 2vw}

.searchIcon{height:1.1vw;transform:translateY(0.1vw)}.search{border:none;outline:none;background:none;font-family:"Roboto", sans-serif;font-size:1.6rem;margin-left:0.2vw;width:15vw}.search-container{display:inline-block;border-bottom:2px solid #8270df;margin:1.5vw 1vw 0 2vw}

.avatar{border-radius:50%;height:2.5vw;margin-right:0.3vw}

.t-header-container{display:flex;flex-direction:row;justify-content:space-between;background:#8270df;margin-top:0.2vw;padding:0 1.2vw 0 1.2vw;height:2vw;border-radius:0 2vw 0 0}.t-header-container .t-col{display:flex;flex:1 1;align-items:center;justify-content:left;color:#fff}.t-header-container .t-col:last-child{flex:0.5 1}.t-header-container .t-header-filter{cursor:pointer}.t-row-container{display:flex;flex-direction:row;justify-content:space-between;margin-top:0.2vw;padding:0 1.2vw 0 1.2vw;height:3vw;border-radius:0 2vw 0 0;border-bottom:0.1vw solid #f5f5f7}.t-row-container .t-col{display:flex;flex:1 1;align-items:center;justify-content:left;color:#000000}.t-row-container .t-col:last-child{flex:0.5 1}

.card-container{height:calc(100% - 3.18vw);background:#fff;margin:0 2vw 0 2vw;border-radius:0 2vw 0 0}

.body-container{background:#f5f5f7;min-height:100%}

*{margin:0;padding:0;box-sizing:border-box}html{font-size:0.65vw;font-family:"Roboto", sans-serif}body{font-size:1.6rem}html,body,#root{height:calc(100% - 3.3vw)}

